.contacts {
  font-size: 24px;
  line-height: 1.4;
  list-style-type: circle;
}

.input {
  width: 300px;
  margin: 20px 0;
  font-size: 30px;
  color: rgba(0, 0, 0, 0.7);

  border: solid 2px grey;
  border-radius: 5px;
}

.btn {
  width: max-content;
  padding: 4px 12px;
  font-size: 18px;
  margin: 20px;
  background: white;
  border: solid 2px grey;
  border-radius: 6px;
}

.btn:hover,
.btn:focus {
  background: rgb(167, 146, 102);
  color: white;
}
