.form {
  background: rgb(167, 146, 102);
  display: flex;
  flex-direction: column;
  border: solid 3px grey;
  border-radius: 8px;
  height: 300px;
  width: 400px;
  padding: 20px;
  font-size: 30px;
}

.input {
  width: 300px;
  margin: 20px 0;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.7);

  border: solid 2px grey;
  border-radius: 5px;
}

.btn {
  width: max-content;
  padding: 4px 12px;
  font-size: 18px;
  margin: 20px;
  background: white;
  border: solid 2px grey;
  border-radius: 6px;
}

.btn:hover,
.btn:focus {
  background: rgb(145, 111, 72);
  color: white;
}
